
  .container {
    border: 1px solid  black;
    border-radius: 10px;
    padding: 20px;
    margin: 20px;
    width: 400px;
    background-color: white;
  }

  h1 {
    margin-bottom: 10px;
  }

  .info {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .info-icon {
    margin-right: 10px;
  }

  .info-text {
    margin-right: 20px;
  }

  .buttons {
    display: flex;
    margin-bottom: 20px;
  }

  .button {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 8px 15px;
    margin-right: 10px;
    cursor: pointer;
  }

  .button.primary {
    background-color: #4CAF50;
    color: white;
  }

  .description {
    margin-bottom: 20px;
  }

  .footer {
    text-align: center;
    font-size: 12px;
    color: #777;
  }