.accueil-image {
  height: 125px;
  width: 125px;
}

.navbar {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 135px;
  flex-wrap: wrap;
  border-bottom: 3px solid #000000;
}

.first-link {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.link1,
.link2 {
  font-size: 20px;
  padding: 5px 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
}

.link1:hover,
.link2:hover {
  background-color: grey;
  border-radius: 10px;
}

.auth-links {
  display: flex;
  gap: 20px;
  position: relative;
}

.navbar li {
  margin: 0 10px;
}

.link1 img {
  display: block;
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    height: auto;
  }
  .link1,
  .link2 {
    font-size: 18px;
  }
}
